$main: #7900C9;
$grey: #707070;

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

header {
    background-color: $main;
    height: 90px;
    display: flex;
    align-items: center;
    padding: 0 55px;
    h1 {
        font-size: 2.2rem;
        color: white;
    }
}

.main-content {
    margin: 64px 80px;

    display: flex;
    nav {
        width: 280px;
        height: 560px;
        border-right: solid 2px #707070;
        h2 {
            font-size: 1.6rem;
        }
        ul {
            list-style: none;
            margin: 16px 0 40px 16px;
            li {
                font-size: 1.5rem;
                cursor: pointer;
                margin-bottom: 20px;
                &:hover {
                    color: $main;
                }
            }
        }
    }
}

.item-container {
    padding: 0 40px;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
}

.item-card {
    border-radius: 20px;
    box-shadow: 1px 2px 4px $grey;
    width: 300px;
    height: 400px;
    padding: 30px;
    transition: 0.5s linear;
    position: relative;
    h2 {
        text-align: center;
        margin-bottom: 20px ;
    }
    img {
        height: auto;
        width: 180px;
        position: relative;
        left: 50%;
        margin-left: -90px;
    }
    &:hover {
        transform: scale(1.01);
    }
}

@media only screen and (max-width: 1366px) {
    header {
        height: 80px;
        h1 {
            font-size: 2rem;
        }
    }

    .main-content {
        nav {
            h2 {
                font-size: 1.4rem;
                padding-right: 20px;
            }
            ul {
                li {
                    font-size: 1.2rem;
                }
            }
        }
    }
    .item-container {
        grid-template-columns: repeat(2, 1fr);
    }
}